body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

ul {
  list-style: none;
  margin: 0;
  padding-left: 0;
}

a {
  text-decoration: none;
}

.container-card {
  max-width: 992px;
  padding-left: 24px;
  padding-right: 24px;
  margin: auto;
  box-sizing: border-box;
}

.row {
  margin-left: -24px;
  display: flex;
  flex-wrap: row;
  margin-right: -24px;
  .col {
    padding-left: 24px;
    flex: 0 0 25%;
    padding-right: 24px;
    box-sizing: border-box;
  }
}

@import "part/header";
@import "part/card";
